import { Token, TokenAmount } from '@pancakeswap-libs/sdk'
import { useMemo } from 'react'
import { MIRROR_TOKENS } from '../../constants'
import { useAllTokenBalances } from '../../state/wallet/hooks'

// compare two token amounts with highest one coming first
function balanceComparator(balanceA?: TokenAmount, balanceB?: TokenAmount) {
  if (balanceA && balanceB) {
    return balanceA.greaterThan(balanceB) ? -1 : balanceA.equalTo(balanceB) ? 0 : 1
  }
  if (balanceA && balanceA.greaterThan('0')) {
    return -1
  }
  if (balanceB && balanceB.greaterThan('0')) {
    return 1
  }
  return 0
}

function isPinned (symbol: string) {
  return MIRROR_TOKENS.some(token => token.symbol === symbol)
}

function pinnedTokenComparator(tokenSymbolA: string, tokenSymbolB: string) {
  // MARS always goes first
  if (tokenSymbolA === 'MARS') {
    return -1
  }
  if (tokenSymbolB === 'MARS') {
    return 1
  }

  // Then other pinned tokens
  if (isPinned(tokenSymbolA) && !isPinned(tokenSymbolB)) {
    return -1
  }
  if (!isPinned(tokenSymbolA) && isPinned(tokenSymbolB)) {
    return 1
  }

  return 0
}

function getTokenComparator(balances: {
  [tokenAddress: string]: TokenAmount | undefined
}): (tokenA: Token, tokenB: Token) => number {
  return function sortTokens(tokenA: Token, tokenB: Token): number {
    // -1 = a is first
    // 1 = b is first

    const pinnedComp = pinnedTokenComparator(tokenA.symbol ?? '', tokenB.symbol ?? '')
    if (pinnedComp !== 0) return pinnedComp

    // sort by balances
    const balanceA = balances[tokenA.address]
    const balanceB = balances[tokenB.address]

    const balanceComp = balanceComparator(balanceA, balanceB)
    if (balanceComp !== 0) return balanceComp

    if (tokenA.symbol && tokenB.symbol) {
      // sort by symbol
      return tokenA.symbol.toLowerCase() < tokenB.symbol.toLowerCase() ? -1 : 1
    }
    return tokenA.symbol ? -1 : tokenB.symbol ? -1 : 0
  }
}

export function useTokenComparator(inverted: boolean): (tokenA: Token, tokenB: Token) => number {
  const balances = useAllTokenBalances()
  const comparator = useMemo(() => getTokenComparator(balances ?? {}), [balances])
  return useMemo(() => {
    if (inverted) {
      return (tokenA: Token, tokenB: Token) => comparator(tokenA, tokenB) * -1
    }
    return comparator
  }, [inverted, comparator])
}

export default useTokenComparator
